body {
  background-color: #2c3e50; /* Slightly lighter shade */
  color: #ffffff;
  font-family: Arial, sans-serif;
}

.app {
  text-align: center;
  margin: 20px;
}

.logo {
  width: 200px;
  height: auto;
  margin-bottom: 20px;
}

.header {
  font-size: 2.5em;
  color: #00aaff; /* Teal color from the logo */
}

.section {
  margin: 20px 0;
}

.input,
.textarea {
  padding: 10px;
  margin: 10px;
  border: 1px solid #00aaff; /* Teal color from the logo */
  border-radius: 5px;
  width: 300px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.textarea {
  height: 100px;
}

.button {
  background-color: #00aaff; /* Teal color from the logo */
  color: white;
  padding: 10px 20px;
  margin: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1em;
}

.button:hover {
  background-color: #0088cc;
}
